.new
    display: flex
    width: 100%
    .newContainer
        flex: 6

        .top, .bottom
            -webkit-box-shadow: 2px 4px 8px 2px rgba(34, 60, 80, 0.2)
            -moz-box-shadow: 2px 4px 8px 2px rgba(34, 60, 80, 0.2)
            box-shadow: 2px 4px 8px 2px rgba(34, 60, 80, 0.2)
            padding: 10px
            margin: 20px
            display: flex

            h1
                color: lightgray
                font-size: 20px

            .left
                flex: 1
                text-align: center

                img
                    width: 100px
                    height: 100px
                    border-radius: 50%
                    object-fit: cover

            .right
                flex: 2

                form
                    display: flex
                    flex-wrap: wrap
                    gap: 30px
                    justify-content: space-around

                    .formInput
                        width: 40%

                        label
                            display: flex
                            align-items: center
                            gap: 10px

                            .icon
                                cursor: pointer

                        input
                            width: 100%
                            padding: 5px
                            border: none
                            border-bottom: 1px solid gray

                    button
                        width: 200px
                        padding: 10px
                        border: none
                        background-color: teal
                        color: #fff
                        font-weight: bold
                        cursor: pointer
                        margin-top: 10px
